import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { documentToHtmlString } from "@contentful/rich-text-html-renderer"
import Typography from "@material-ui/core/Typography"
import Grid from "@material-ui/core/Grid"
import Img from "gatsby-image"
import { graphql } from "gatsby"
import { options } from "../option"

export default ({ data }) => {
  const product = data.contentfulProduct
  const text = product.description ? product.description.json : null
  const image = product.photo.fixed;

  return (
    <Layout>
      <SEO title={product.title} description={product.metaDescription} />
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          {" "}
          <Typography variant="h5" component="h1">
            {product.name}
          </Typography>
          {/*<Price price={product.price} newPrice={product.newPrice} unit={'р.'}/>*/}
          {text ? (
            <>
              <Typography variant="h6" component="h2">
                Описание товара
              </Typography>
              <div
                dangerouslySetInnerHTML={{
                  __html: documentToHtmlString(text, options),
                }}
              />
            </>
          ) : null}
        </Grid>
        <Grid
          item
          xs={12}
          md={6}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {image ? <Img fixed={image} /> : null}
        </Grid>
      </Grid>
    </Layout>
  )
}

export const pageQuery = graphql`
  query Product($name: String!) {
    site {
      siteMetadata {
        title
      }
    }
    contentfulProduct(name: { eq: $name }) {
      name
      photo {
        fixed {
          ...GatsbyContentfulFixed_tracedSVG
        }
      }
      description {
        json
      }
      price
      newPrice
      title
      metaDescription
    }
  }
`
